<template>
  <div v-if="!route.hidden">
    <el-menu-item v-if="onlyOne()" :index="onlyChildren.path" @click="routeLink(onlyChildren.path)">
      <i :class="onlyChildren.meta.icon"></i>
      <span slot="title">{{onlyChildren.meta.title}}</span>
    </el-menu-item>
    <el-submenu :index="route.path" v-else>
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">{{route.meta.title}}</span>
        </template>
        <sidebar-item v-for="(childrenRoute,i) in route.children" :key="i" :route="childrenRoute" :basurl="setPath(basurl , childrenRoute.path)" />
      </el-submenu>
  </div>
</template>
<script>
  export default {
    name: "SidebarItem",
    props: ['route','basurl'],
    data(){
      return{
        onlyChildren:''
      }
    },
    methods: {
      setPath(basurl ,path) {
        let _url = basurl==='/'?'':basurl
          return _url+ '/' + path
      },
      // 判断是否有子集，并且设置baseurl
      onlyOne(){
        let childrenRouteArr = this.route.children || []
        let routeArr = childrenRouteArr.filter((childrenRoute)=>{
            return !childrenRoute.hidden
        })
        
        if(routeArr.length > 1){
          return false
        }
        if(routeArr.length === 1){
          let _url = (this.basurl==='/'?'':this.basurl) 
            this.onlyChildren  = { ...routeArr[0], path:_url + '/' + routeArr[0].path}

          if(routeArr[0].children){
            // 满足上面条件的 子路由 则这个子路由没有子路由
            return routeArr[0].children.length === 0 && !this.route.alwaysShow
          } else{
            // 满足上面条件的 子路由 则这个子路由没有子路由
            return true && !this.route.alwaysShow
          }
        }else{
          // 没有显示在侧边栏的子路由
          this.onlyChildren = { ...this.route,  path:this.basurl}
          return true && !this.route.alwaysShow
        }
      },
      routeLink(path){
        this.$router.push(path)
      }
    }
  }
  // 根据路由来显示侧边栏，通过下面的条件来判断是否是多级的路由
  // 1.只有 一个 显示在侧边栏 的子路由 或者 没有显示在侧边栏的子路由
  // &&
  // 2.如果是满足上面条件的 子路由 则这个子路由没有子路由
  // &&
  // 3.alwaysShow为false，如果为true则一定是多级路由
  
  // 侧边栏高亮显示，两种情况，1.就是该路由，2.该路由跳转到下面的子路由
  // 其实 ：index绑定的就是 basurl + 当前路由（路由有重定向），如果basurl和当前路由一样就不用加了（没有显示子路由的情况）
</script>