import request from '@/utils/request'

export function login() {
  return request({
    url: 'login',
    method: 'post',
  })
}

export function getInfo(token) {
  return request({
    url: 'user-info',
    method: 'post',
    params: {
      token
    }
  })
}

export function logout() {
  return request({
    url: '/logout',
    method: 'post'
  })
}