
import {
	getInfo,login
} from '@/api/user'
import {setToken} from '@/utils/auth'
import {asyncRoutes}  from '@/router/routes'

const filterRourter = function(routes,roles){
    let arr = []
    routes.forEach(route=>{
        if(route.meta &&route.meta.roles && route.meta.roles.length>0){
            if(roles.some(item=>route.meta.roles.includes(item))){
                if (route.children) {
                    route.children = filterRourter(route.children, roles)
                  }
                arr.push(route)
            }
        }else{
            arr.push(route)
        }
    })
    return arr
}

export default {
    // 获取用户信息
    getInfo({
        commit,
        state
    }) {
        return new Promise((r, e) => {
            getInfo(state.token).then((res) => {
                if (res.data) {
                    r(res.data)
                    commit('SET_NAME', res.data.name)
                    commit('SET_ROLES', res.data.roles)
                } else {
                    e()
                }
            })

        })
    },
    // 登录
    login({commit}){
        return new Promise((r,e)=>{
            login().then((res)=>{
                setToken(res.data.token)
                commit('SET_TOKEN',res.data.token)
                // this.$router.replace({path:this.$route.query.redirect})
                if(res.data.token){
                    r(res)
                }else{
                    e(res)
                }
               })
        })
    },
    // 生成路由
    generateRoutes(store,roles){
        return new Promise((r)=>{
            // 把异步路由挨个进行匹配，得到一个新的路由数据
            let arr = filterRourter(asyncRoutes,roles)
            store.commit('SET_ROUTES',arr)
           r(arr) 
        })
    },
    // 改变侧边栏展开收起
    changeSideBarClose(store){
        store.commit('SET_SIDEBARCLOSE')
    }
}