<template>
  <div>
    <el-menu :default-active="getPath" @open="handleOpen" @close="handleClose" class="el-menu-vertical-demo"
     :collapse="sideBarClose"
     background-color="#304156"
     text-color="#bfcbd9"
     >
        <sidebar-item v-for="(route,i) in getRoutes" :key="i" :route="route" :basurl="route.path" />
    </el-menu>

  </div>
</template>
<script>
import sidebarItem from './sidebarItem'
  import { mapGetters,mapState } from 'vuex'
  export default {
    components:{sidebarItem},
    data() {
      return {
        isCollapse: true
      };
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath)
      },
    },
    computed: {
      ...mapGetters([
        "getRoutes"
      ]),
      ...mapState(['sideBarClose']),
      // 获取当前路径，使得侧边栏高亮显示
      getPath(){
        return this.$route.path
      }
      
    }
  }
</script>
<style lang="scss" scoped>
// 这里给这个块元素设置宽度，要不然没有动画效果太丑了
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 160px;
    min-height: 400px;
  }
  .el-menu-vertical-demo{
    border: 0;
  }
</style>