import layout from '@/layout'
export default [{
    path: '/',
    component: layout,
    redirect: '/admin',
    meta: {
      title: 'root',
      icon: 'el-icon-s-promotion',
      roles: ['admin'] // you can set roles in root nav
    },
    children: [{
      path: 'admin',
      component: () => import('@/views/admin'),
      meta: {
        title: 'admin一级01',
        icon: 'el-icon-s-promotion',
      },
    },
    // {
    //   path: 'admin2',
    //   component: () => import('@/views/admin'),
    //   // redirect:'admin2/admin001',
    //   meta: {
    //     title: 'admin一级02',
    //     icon: 'lock',
    //   },
    //   children: [{
    //     path: 'admin001',
    //     component: () => import('@/views/admin'),
    //     meta: {
    //       title: 'admin二级01',
    //       icon: 'lock',
    //     },
    //   },{
    //     path: 'admin002',
    //     component: () => import('@/views/admin'),
    //     // hidden:true,
    //     meta: {
    //       title: 'admin二级02',
    //       icon: 'lock',
    //     },
    //   }]
    // }
  ]
  },
  {
    path: '/login',
    hidden: true,
    component: () => import('@/views/login')
  },
  {
    path: '/404',
    hidden: true,
    component: () => import('@/views/errPage/404')
  }
]
export const asyncRoutes = [{
    path: '/roles',
    component: layout,
    redirect: '/roles/index',
    meta: {
      title: 'roles',
      icon: 'el-icon-lock',
      roles: ['admin'] // you can set roles in root nav
    },
    children: [{
      path: 'index',
      component: () => import('@/views/roles'),
      meta: {
        title: 'roles',
        icon: 'el-icon-lock',
        roles: ['admin'] // you can set roles in root nav
      },
    }]
  },
  {
    path: '/word',
    component: layout,
    redirect: '/word/index',
    meta: {
      title: 'word',
      icon: 'el-icon-folder',
      roles: ['edit'] // you can set roles in root nav
    },
    children: [{
      path: 'index',
      component: () => import('@/views/word'),
      meta: {
        title: 'word',
        icon: 'el-icon-folder',
        roles: ['edit'] // you can set roles in root nav
      },
    }]
  },
  {
    path: '/power',
    component: layout,
    redirect: '/power/index',
    meta: {
      title: 'power',
      icon: 'el-icon-view',
      roles: ['admin'] // you can set roles in root nav
    },
    children: [{
      path: 'index',
      component: () => import('@/views/power'),
      meta: {
        title: 'power',
        icon: 'el-icon-view',
        roles: ['admin'] // you can set roles in root nav
      },
    }]
  },
  // 这里必须最后定义不然会全部拦截
  {
    path: '*',
    redirect: '/404',
    hidden: true
  }
]