import router from './router'
import store from './store'
import {
    getToken
} from './utils/auth'

const whiteList = ['/login']

router.beforeEach(async (to, from, next) => {
    const hasToken = getToken()
    if (hasToken) {
        // 如果有token则证明已经登录，不应该在出现登录界面，这里处理下
        // if(to.path === '/login'){
        //     next('/admin')
        // }
        if(to.path === '/login'){
            next('/admin')
        }else{ 
            //下面的next要写在else里面，不然会执行代码，next和return不同
            // 判断是否获取到user-info
            if(store.state.roles&& store.state.roles.length>0){
                next()
            }else{
                const {roles } = await store.dispatch('getInfo') 
                // 获取到角色的路由权限后，过滤异步路由，得到可访问的路由，然后addrouter
                const accessRoutes =await store.dispatch('generateRoutes',roles)
                    router.addRoutes(accessRoutes)
                    next({ ...to, replace: true })
            }                    
            // next()
        }
    } else {
        // 如果没有token，判断当前路由是否在白名单内，是的话则跳转的该路由，不是则跳转指定路由--登录页面
        //to.path获取当前路由，'/admin,/roles/index',注意在路由配置中的路由跳转，如/roles跳到了/roles/index
        if(whiteList.includes(to.path)){
            next()
        } else{
            next(`/login?redirect=${to.path}`)
        }
    }
})